import fs from 'fs';
import path from 'path';
import { v4 as uuidv4 } from 'uuid';

const STUDIO_DB_PATH = path.join(process.cwd(), 'data', 'studios.json');

// --- Types ---
export interface TimeSlotPrice {
    start: string;
    end: string;
    price: number;
    pricingType?: 'fixed' | 'discount';
    discountRate?: number; // Percentage (e.g., 20 for 20% off)
}
export interface DaySchedule {
    slots: TimeSlotPrice[];
}
export interface RoomPricing {
    weekday: DaySchedule;
    saturday: DaySchedule;
    sundayHoliday: DaySchedule;
}
export interface Room {
    id: string; // generated uuid or simple ID
    name: string;
    description?: string;
    images?: string[];
    basePrice: number;
    pricing: RoomPricing;
}
export interface EquipmentOption {
    name: string;
    pricePerHour: number;
}
export interface BusinessHours {
    weekday: string;
    saturday: string;
    sundayHoliday: string;
}
export interface StaffMember {
    id: string;
    name: string;
    email: string;
    password?: string;
    role: 'admin' | 'staff';
    createdAt: string;
}

export interface BlacklistEntry {
    userId: string;
    userName: string;
    email?: string;
    reason: string;
    createdAt: string;
}

export interface PersonalPracticeSettings {
    enabled: boolean;
    reservationWindowType: 'hours' | 'days';
    reservationWindowValue: number;
    maxPeople: number;
}

export interface DesignSettings {
    logoSize: number; // percentage
    backgroundColor: string;
    backgroundImageUrl?: string;
    backgroundType: 'color' | 'image';
}

export interface StudioProfile {
    id: string; // UUID
    storeName: string;
    companyName?: string;
    representative: string;
    representativeEmail?: string;
    manager: string; // 担当者名 (or using contactPerson)
    contactPerson: string;
    contactPersonEmail?: string;
    postalCode?: string;
    address: string;
    phone: string;
    email: string; // 店舗全体メール
    businessHours: BusinessHours;
    url: string;
    studioCount: number;
    rooms: Room[];
    equipmentOptions: EquipmentOption[];
    options?: string;
    images?: string[]; // Array of image URLs or base64
    appealPoint?: string; // Marketing text
    monthlyRevenueTarget?: number; // Target for Analytics
    logoUrl?: string; // Studio Logo
    invoiceNumber?: string; // Qualified Invoice Issuer Number (T-number)
    staff?: StaffMember[];
    blacklist?: BlacklistEntry[];
    studentDiscount?: {
        enabled: boolean;
        discountType: 'amount' | 'percentage';
        value: number;
    };
    otherDiscounts?: Array<{
        name: string;
        enabled: boolean;
        discountType: 'amount' | 'percentage';
        value: number;
    }>;
    personalPracticeSettings?: PersonalPracticeSettings;
    designSettings?: DesignSettings;
    createdAt: string;
}

// Initialize Studio DB as Array
if (!fs.existsSync(STUDIO_DB_PATH)) {
    fs.writeFileSync(STUDIO_DB_PATH, JSON.stringify([], null, 2));
}

// --- Helpers ---

export const getAllStudios = (): StudioProfile[] => {
    try {
        const data = fs.readFileSync(STUDIO_DB_PATH, 'utf-8');
        return JSON.parse(data);
    } catch (error) {
        return [];
    }
};

export const getStudioById = (id: string): StudioProfile | null => {
    const studios = getAllStudios();
    return studios.find(s => s.id === id) || null;
};

export const saveStudio = (profile: StudioProfile): void => {
    const studios = getAllStudios();
    const index = studios.findIndex(s => s.id === profile.id);

    if (index >= 0) {
        studios[index] = profile;
    } else {
        studios.push(profile);
    }

    fs.writeFileSync(STUDIO_DB_PATH, JSON.stringify(studios, null, 2));
};

export const deleteStudio = (id: string): void => {
    const studios = getAllStudios().filter(s => s.id !== id);
    fs.writeFileSync(STUDIO_DB_PATH, JSON.stringify(studios, null, 2));
}

export const createEmptyStudio = (): StudioProfile => {
    return {
        id: uuidv4(),
        storeName: "New Studio",
        representative: "",
        manager: "",
        contactPerson: "",
        address: "",
        phone: "",
        email: "",
        businessHours: { weekday: "10:00-22:00", saturday: "10:00-22:00", sundayHoliday: "10:00-22:00" },
        url: "",
        studioCount: 0,
        rooms: [],
        studentDiscount: { enabled: false, discountType: 'amount', value: 0 },
        otherDiscounts: [],
        personalPracticeSettings: { enabled: true, reservationWindowType: 'days', reservationWindowValue: 1, maxPeople: 2 },
        designSettings: { logoSize: 100, backgroundColor: "#000000", backgroundType: 'color' },
        equipmentOptions: [],
        createdAt: new Date().toISOString()
    };
};
